.About {

    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-size: 100%;
    height: 100%;

    transition:
    opacity 2s,
    display 2s;
    background-color: rgb(0, 0, 0);

  transition-behavior: allow-discrete;
  @starting-style {
    opacity: 0;
  }


}

.aboultImage{
    display: block;
    position: absolute;
    width: 100%;
    z-index: 0;
    opacity: 0.2;
    height: 80%;
}

.aboutTitle{
    padding: 10px;
    margin: 10px;
    color: rgb(255, 255, 255);
    position: relative;
    z-index: 1;
}

.aboutSubject{
    color: white;
    text-align: center;
    font-size: 1.2em;
    padding: 10px;
    margin: 10px;
    position: relative;
    z-index: 2;
    font-weight: 500;
}

@media screen and (max-width: 768px) { 
  .aboultImage{
    height: 100%;
  }


}
