header{
    height: 100%;
    width: 100%;
}

.Home {
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-size: 100%;
    height: 100%;
    transition:
    opacity 2s,
    display 2s;
    background-color: rgb(0, 0, 0);

  transition-behavior: allow-discrete;
  @starting-style {
    opacity: 0;
  }


}

.homeImage{
    display: block;
    position: absolute;
    z-index: 0;
    opacity: 0.3;
    width: 100%;
    height: 100%;

}

.homeTitle{
    padding: 10px; 
    margin: 5px;
    color: rgb(255, 255, 255);
    position: relative;
    z-index: 1;
   
}

.homeSubject{
    color: white;
    text-align: center;
    font-size: 1.2em;
    padding: 10px;
    position: relative;
    z-index: 2;
    font-weight: 500;
}

@media screen and (max-width: 768px) { 
    .homeImage{
      height: 70%;
    }

    .homeTitle{
       top: 20px;
       
    }
  
  
  }