nav {
    position: fixed;
    width: 100%;
    padding-top: 10px;
    z-index: 999;
    display: flex;
    justify-content: center;
    background-color: #000;
    padding: 15px;
}

nav a {
    margin: 0 10px;
    padding: 5px;
    text-decoration: none;
    color: #ffffff;
    cursor: pointer;
}

nav a:hover {
    color: #ccc;
    background-color: #ff0000;
    border-radius: 10px;
}

a:active {
    background-color: #ff0404;
    color: #ff0000;
}

.navImage{
    width: 35px;
    height: 30px;
}


@media screen and (max-width: 768px) { 
    .navImage{
        width: 30px;
        height: 25px;
    }

  
    
}